/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 70%; /* 1rem = 10px for easier rem calculations */
  scroll-behavior: smooth;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: #f4f6f8;
  color: #2b2b2b;
  line-height: 1.7;
  font-size: 1.8rem; /* Default font size */
}

/* Navbar */
.navbar {
  background: #ffffff;
  box-shadow: 0 0.1rem 0.6rem rgba(0, 0, 0, 0.05);
  position: sticky;
  top: 0;
  z-index: 999;
}

.nav-container {
  max-width: 110rem;
  margin: auto;
  padding: 1.6rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-logo {
  font-weight: bold;
  font-size: 2rem;
  color: #007acc;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 2rem;
}

.nav-links a {
  text-decoration: none;
  color: #444;
  font-weight: 500;
  font-size: 1.4rem;
  transition: color 0.3s;
}

.nav-links a:hover {
  color: #007acc;
}

/* Hero */
.hero {
  background: linear-gradient(135deg, #007acc, #00c9ff);
  color: white;
  padding: 10rem 2rem;
}

.hero .container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  gap: 2rem;
  max-width: 110rem;
  margin: 0 auto;
}

.profile-img {
  justify-self: center;
  width: 22rem;
  height: 22rem;
  object-fit: cover;
  border-radius: 50%;
  border: 0.6rem solid white;
  box-shadow: 0 0.6rem 2rem rgba(0, 0, 0, 0.15);
}

.hero-text {
  text-align: left;
}

.name {
  font-size: 3.2rem;
  font-weight: 700;
}

.title {
  font-size: 2rem;
  margin-top: 0.5rem;
  color: #e0f4ff;
}

.intro {
  margin-top: 2rem;
  font-size: 1.6rem;
  max-width: 60rem;
}

/* Sections */
.section:nth-child(even) {
  background-color: #ffffff;
}

.section:nth-child(odd) {
  background-color: #f0f8ff;
}

.section {
  padding: 12rem 8rem;
  text-align: center;
  animation: fadeInUp 1s ease forwards;
  opacity: 0;
  transform: translateY(2rem);
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.section h2 {
  font-size: 2.5rem;
  margin-bottom: 4.5rem;
  color: #007acc;
}

/* Entry blocks */
.entry {
  margin-bottom: 2.5rem;
  text-align: left;
  max-width: 80rem;
  margin-left: auto;
  margin-right: auto;
}

.entry h3 {
  font-size: 1.9rem;
  color: #007acc;
  margin-bottom: 0.5rem;
}

/* Skills */
.skills-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin-top: 2rem;
  list-style: none;
}

.skills-list li {
  background-color: #e6f2ff;
  color: #007acc;
  padding: 1rem 2rem;
  font-size: 1.4rem;
  border-radius: 3rem;
  transition: all 0.3s ease;
  cursor: default;
}

.skills-list li:hover {
  background-color: #007acc;
  color: white;
  transform: translateY(-0.2rem);
  box-shadow: 0 0.4rem 1rem rgba(0, 122, 204, 0.2);
}

/* Certifications */
.cert-list {
  list-style-type: disc;
  padding-left: 2rem;

  max-width: 60rem;
  margin: 0 auto;
}

.cert-list li {
  margin-bottom: 1rem;
}

/* Footer */
.footer {
  padding: 2.5rem;
  text-align: center;
  font-size: 1.2rem;
  color: #777;
  background-color: #ffffff;
}
